* {
  margin: 0;
  padding: 0; }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

button {
  cursor: pointer; }

html,
body {
  margin: 0;
  padding: 0;
  font-family: 'TT Lakes', 'Arial', sans-serif;
  font-size: 16px;
  background-color: #f2f2f2; }

.header {
  text-transform: uppercase;
  margin: 0;
  padding: 16px 16px;
  background: linear-gradient(90deg, #f8f8f8 0%, #fff 98.61%); }

.header::before {
  content: '';
  display: block;
  position: absolute;
  transform: translate(-16px, -16px);
  width: 100vw;
  height: 1px;
  z-index: 1;
  border-bottom: 1px solid #d9fff5; }

.main-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100vh; }

.container {
  width: 95%;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-wrap: wrap; }

@media screen and (max-width: 768px) {
  body {
    height: 100%;
    background-image: linear-gradient(to bottom, #fff 0%, #fff 88px, #f8f8f8 88px, #f8f8f8 100%); }
  .container {
    width: 90%; }
  .header {
    font-size: 16px; } }

@media screen and (min-width: 768px) {
  .mobview {
    display: none; }
  .header {
    padding: 16px 3%;
    height: 56px;
    background: linear-gradient(90deg, #f8f8f8 1.39%, #ffffff 100%); } }

@media screen and (min-width: 768px) and (max-width: 1440px) {
  body {
    background-image: linear-gradient(to bottom, #ffffff 88px, #f8f8f8 88px); } }

@media screen and (min-width: 1440px) {
  body {
    background-color: #f8f8f8; }
  .main-wrapper {
    margin-left: 320px;
    width: calc(100% - 320px); } }

.hiddenItem {
  display: none; }
